* {
   margin: 0;
   padding: 0;
}


.top {
   background: linear-gradient(to right, #E5E5E5 60%, #272727 40%);

   .top_center {
   display: flex;
   flex-wrap: wrap;
   }
   .top_content {

      margin: 0 162px;
      min-height: 100vh;
      padding-bottom: 60px;
   }

   .top_menu {
      display: flex;
      justify-content: space-between;
      padding-top: 70px;
   }

   .menu_list {
      display: flex;
      text-decoration: none;
      list-style-type: none;

   }

   .menu_link {
      padding-left: 40px;
   }

   .menu {
      padding-left: 100px;
   }

   .top_center {
      margin-top: 330px;
      margin-bottom: 280px;
   }
   .top_img {
      margin-left: 22.5vw;
   }
   .look_collection {
      display: flex;
            padding-top: 50px;
   
      .hr {
         min-width: 130px;
         border: 1px #272727;
         margin-top: 14px;
      }
   
      .look_collection__heading {
         padding-left: 38px;
   
      }
   
      .look_collection__heading_link {
         font-weight: 300;
         font-size: 18px;
         line-height: 24px;
         display: flex;
         align-items: center;
         color: #272727;
         text-decoration: none;
      }
   
   
   
   }

}

.card_text {
   display: flex;
   flex-direction: column;
   align-items: flex-end;
   

   .card_name {
      font-weight: 400;
      font-size: 16px;
      line-height: 21px;
      text-align: right;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      color: #FFFFFF;
   }

   .card_material {
      font-weight: 300;
      font-size: 18px;
      line-height: 23px;
      text-align: right;
      color: #C2C2C2;
   }

   .card_price {
      font-weight: 300;
      font-size: 18px;
      line-height: 24px;
      letter-spacing: 0.03em;
      color: #EFC58B;
   }
}

.for_mw {
   display: flex;
   justify-content: space-between;
   .for_man {
      position: relative;
      margin-right: 21px;
      width: 100%;
   }
   .for_man__block {
      position: absolute;
      top: 227px;
      left: 128px;
         }
   .for_woman__block {
      position: relative;
       }
   .text {
      font-weight: 300;
   font-size: 24px;
   line-height: 31px;
   text-align: right;
   color: #FFFFFF;
   }
   .for_man_heading {
      font-weight: 400;
   font-size: 64px;
   line-height: 71px;
   text-align: center;
   letter-spacing: 0.03em;
   color: #FFFFFF;
   }
   .for_man_hr {
      width: 130px;
border: 4px  #FFFFFF;
   }
}
